.card {
  background: #fff;
  border-radius: 2px;
  height: 225px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  color: red;
  text-align: center;
}

.card > .img-container {
  height: 90%;
  overflow: hidden;
  text-align: center;
  background: black;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;

}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top:5px;
  font-size: 20px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.jumbotron {
    background-image: url(/static/media/CastlevaniaBig.5157f51a.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center; 
    position: relative;
    background-color: black;
    height: 250px;

  }

h2, h3, .scoreBoard{
    width: 100%;
    text-align: center;
    color: red;
}

.navbar{
    background-color: lightgrey;
    border: red solid 2px;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

